#imgModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 99;

  display: flex;
  justify-content: center;
  align-items: center;

  display: none;

  &.on {
    display: flex;
  }

  img {
    max-width: 1280px;
  }
}
